import React from 'react'
import { Link } from 'react-router-dom';

export default function UserBag({order, auth}) {
  return (
    <div>
        {order?.items.length == 0 ? 
        <div className="order__clear">
            <h2>  </h2>
            <p>  <Link to="/"> </Link>    </p>
        </div> : 
        order?.items.map((item)=>
        <div key={item.number} className="bag__block">
            <div className="block__img"> 
                <img src={item.img} alt={`  ${item.number}`}/>
            </div>
            <div className="block__content"> 
                <p className="orderNumber">  {item.number}</p>
                <p className='rent'>{item.delivery == "" ? ` :` : " :"} <span>{item.date == '' && !item.orderReceived ? "  " : item.orderReceived ? " " : item.date}</span></p>
                <Link className="track" to={`/user/${auth?.userData.idUser}/${item.number}`}>{!item.orderReceived ? " " : " " }</Link>
            </div>
        </div>
        )
        }
    </div>
  )
}
