import React,  { useState, useEffect}  from 'react'
import ProductCard from "../shared/componets/ProdactCard"
import { Link } from 'react-router-dom'
import FurnitureSlider from "../shared/componets/slider/furniture"
import CubinsSlider from "../shared/componets/slider/cubins"
import {cardsSlice, getCards} from "../shared/store/slices/cards"
import { useSelector } from 'react-redux'
import { store } from '../shared/store/slices/store';
import "./scss/main.scss"

export default function MainPage() {
    const [showSelect, setShowSelect] = useState({
        active: " ",
        state: false,
        src: "/emptyCubins.webp"
    })
    const cards = useSelector((state)=>state.cardsSlice)
    useEffect(()=>{
        store.dispatch(getCards({}))
      }, [])
    return (
        <main>
            <div className="fd-row">
                <div className="slider">
                    <div className="slides">
                        <input type="radio" name="r" id="r1" defaultChecked/>
                        <input type="radio" name="r" id="r2"/>
                        <input type="radio" name="r" id="r3"/>
                        <input type="radio" name="r" id="r4"/>
                        <div className="slide s1">
                            <div className="slide__contant">
                                <div className="slide__text">
                                    <h2>  </h2>
                                    <p>   5000</p>
                                </div>
                                <div className="slide__button">
                                    <Link to="/card/2" className="more"></Link>
                                </div>
                            </div>
                            <div className="slide__radius">
                                <img decoding="async" src="/uploads/1-cubinsForLive.webp" alt="  "/>
                            </div>
                        </div>
                        <div className="slide">
                            <div className="slide__contant">
                                <div className="slide__text">
                                    <h2>  </h2>
                                    <p>   </p>
                                </div>
                                <div className="slide__button">
                                    <Link to="/card/5" className="more"></Link>
                                </div>
                            </div>
                            <div className="slide__radius">
                                <img decoding="async" src="/uploads/4-cubinsForwareHouse.webp" alt="  "/>
                            </div>
                        </div>
                        <div className="slide">
                            <div className="slide__contant">
                                <div className="slide__text">
                                    <h2> c </h2>
                                    <p> </p>
                                </div>
                                <div className="slide__button">
                                    <Link to="/card/3" className="more"></Link>
                                </div>
                            </div>
                            <div className="slide__radius">
                                <img decoding="async" src="/uploads/3-cubinsForBath.webp" alt="  "/>
                            </div>
                        </div>
                        <div className="slide">
                            <div className="slide__contant">
                                <div className="slide__text">
                                    <h2>  </h2>
                                    <p>   5000</p>
                                </div>
                                <div className="slide__button">
                                    <Link to="/card/4" className="more"></Link>
                                </div>
                            </div>
                            <div className="slide__radius">
                                <img decoding="async" src="/uploads/5-cubinsForWork.webp" alt=" "/>
                            </div>
                        </div>
                        <div className="navigation-auto">
                            <div className="auto-btn1"></div>
                            <div className="auto-btn2"></div>
                            <div className="auto-btn3"></div>
                            <div className="auto-btn4"></div>
                        </div>
                    </div>
                    <div className="navigation">
                        <label htmlFor="r1" className="navigation__bar"></label>
                        <label htmlFor="r2" className="navigation__bar"></label>
                        <label htmlFor="r3" className="navigation__bar"></label>
                        <label htmlFor="r4" className="navigation__bar"></label>
                    </div>
                </div>
                <div className="one__card">
                    <ProductCard key={0} item={cards.items[0]}/>
                </div>
            </div>
            <div className="line"></div>
            <div className="advantages-row">
                <div className="advantages">
                    <img decoding="async" src="/money.svg" alt=""/>
                    <div className="advantages__content">
                        <h2>  </h2>
                        <p>       </p>
                    </div>
                </div>
                <div className="advantages">
                    <img decoding="async" src="/discount.svg" alt=""/>
                    <div className="advantages__content">
                        <h2>  3 </h2>
                        <p>     </p>
                    </div>
                </div>
                <div className="advantages">
                    <img decoding="async" src="/services.svg" alt=""/>
                    <div className="advantages__content">                     
                        <h2> </h2>
                        <p>       </p>
                    </div>
                </div>
            </div> 
            <div className="line"></div>
            <div className="fd-row">
                <div className="content">
                    <h3>MSB</h3>
                    <h2>C    </h2>
                    <p>         </p>
                    <div className="fd-row">
                        <Link to="/" className="content__Youtube"><div className="block1"></div></Link>
                        <Link to="/"> <p className="content__text"> Youtube </p></Link>
                    </div>
                </div>
                <div className="choose">
                    <div className="choose__fd-col">
                        <p className="choose__more" onClick={()=>setShowSelect({...showSelect, state: true})} >{showSelect.active}</p>
                        {showSelect.state && <div className='select'>
                                <p onClick={()=>setShowSelect({active: " ", state: false, src: "/emptyCubins.webp"})}> </p>
                                <p onClick={()=>setShowSelect({active: " ", state: false, src: "/uploads/cubinsForDress.webp"})}> </p>
                                <p onClick={()=>setShowSelect({active: " ", state: false, src: "/uploads/cubinsForWork.webp"})}> </p>
                                <p onClick={()=>setShowSelect({active: "  ", state: false, src: "/uploads/cubinsForLive.webp"})}>   </p>
                                <p onClick={()=>setShowSelect({active: "  ", state: false, src: "/uploads/cubinsForBath.webp"})}>   </p>
                        </div>}
                    </div>

                <div className="choose__img"><img decoding="async" src={showSelect.src} alt={showSelect.active}/></div> 
                </div>
            </div>
            <FurnitureSlider/>
            <div className="line"></div>
            <CubinsSlider/>
            <div className="line"></div>
        </main>
    )
}
